import React, { ReactNode } from "react";

export enum CheckStatus {
  UNCHECK = "uncheck",
  CHECKED = "checked",
  HALFCHECK = "halfcheck",
}

// 
export enum DragHover {
  TOP = "top",
  INSIDE = "inside",
  BOTTOM = "bottom",
}

export interface TreeProps {
  style?: React.CSSProperties;
  className?: string;

  /**
   * @zh 
   */
  dataList?: any[];

  /**
   * @zh 
   */
  showLine?: boolean;

  /**
   * @zh 
   */
  draggable?: boolean;

  /**
   * @zh 
   * @defaultValue false
   */
  enableCheck?: boolean;

  /**
   * @zh  enableChecktrue
   */
  checkboxReadOnly?: boolean;

  /**
   * @zh 
   */
  isExpandAllKeys?: boolean;

  /**
   * @zh 
   */
  defaultExpandKeys?: string[];
  /**
   * @zh 
   */
  onExpandKeysChange?: (_keyArr: string[]) => void;

  /**
   * @zh  enableChecktrue
   */
  defaultCheckedKeys?: [];

  /**
   * @zh  enableChecktrue
   */
  checkedKeys?: [];

  /**
   * @zh  dataList 
   */
  fieldNames?: {
    key?: string;
    title?: string;
    parent?: string;
    children?: string;
  };

  /**
   * @zh 
   */
  onCheck?: () => void;

  /**
   * @zh 
   */
  render?: ReactNode;

  /**
   * @zh icon
   */
  showIcon?: boolean;

  /**
   * @zh 
   */
  onNodeClick?: () => void;

  /**
   * @zh 
   */
  onMultiSelect?: () => void;

  /**
   * @zh enableChecktrue
   */
  onCheckAll?: () => void;

  /**
   * @zh 
   */
  onRightClick?: (e:React.MouseEvent,nodeData?:any) => void;

  /**
   * @zh 
   */
  selectedKeys?: string[];

  /**
   * @zh tree
   */
  onOutSideClick?: () => void;

  /**
   * @zh 
   */
  enableVirtualList?: boolean;

  /**
   * @zh 
   */
  nodeSort?: any;

  checkLeafNode: (nodeItem: any) => boolean;

  rootFilter?: (pre: any, after: any) => any[];
}

export interface treeContextProps {
  [key: string]: any;
}

export interface TreeNodeProps extends React.ReactElement {
  nodeKey: string;
  data: any;
  title: string;
  disabled: boolean;
  perfixCls: string;
  style: React.CSSProperties;
}
